// Plain JavaScript utility file - utils.js
// This tests that .js files work alongside .ts files

/**
 * Format a date as a human-readable string
 * @param {Date} date
 * @returns {string}
 */
function formatDate(date) {
    return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
    });
}

/**
 * Debounce a function call
 * @param {Function} func
 * @param {number} wait
 * @returns {Function}
 */
function debounce(func, wait) {
    let timeout;
    return function executedFunction(...args) {
        const later = () => {
            clearTimeout(timeout);
            func(...args);
        };
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
    };
}

/**
 * Simple DOM ready handler
 * @param {Function} fn
 */
function ready(fn) {
    if (document.readyState !== 'loading') {
        fn();
    } else {
        document.addEventListener('DOMContentLoaded', fn);
    }
}

// Log that plain JS is working
ready(function() {
    console.log('[utils.js] Plain JavaScript utilities loaded');
});

// Export for ES modules
export { formatDate, debounce, ready };
