// Global TypeScript file - site.ts
// This demonstrates TypeScript compilation in MvcFrontendKit

interface AppConfig {
    name: string;
    version: string;
    debug: boolean;
}

const config: AppConfig = {
    name: 'TestKit',
    version: '1.0.0',
    debug: true
};

function log(message: string): void {
    if (config.debug) {
        console.log(`[${config.name}] ${message}`);
    }
}

function initApp(): void {
    log('Application initialized');
    log(`Version: ${config.version}`);

    // Add a visual indicator that TypeScript is working
    const body = document.body;
    if (body) {
        const indicator = document.createElement('div');
        indicator.id = 'ts-indicator';
        indicator.textContent = 'TypeScript is working!';
        indicator.style.cssText = 'position: fixed; bottom: 60px; right: 10px; background: #3178c6; color: white; padding: 8px 16px; border-radius: 4px; font-size: 12px; z-index: 9999;';
        body.appendChild(indicator);
    }
}

// Export for ES modules
export { config, log, initApp };

// Also make available globally for non-module scripts
(window as any).TestKit = { config, log, initApp };

// Initialize on DOMContentLoaded
document.addEventListener('DOMContentLoaded', initApp);
